import React, { useState, useEffect } from "react";
import * as ScrollMagic from "scrollmagic";
import useWindowSize from "../../../../Hooks/UseWindowSize";
import gsap from "gsap";
import "./SideImg.scss";

const SideImg = ({ id, imgSrc, caption, text, style, side="left", swapOnMobile=false}) => {
  const size = useWindowSize();
  const { width } = size;
  var { imgOnLeft } = false;
  const [scrollMagic, setScrollMagic] = useState({
    controller: new ScrollMagic.Controller(),
    timelineOne: gsap.timeline(),
  });
  const { controller, timelineOne } = scrollMagic;

  useEffect(() => {

    timelineOne.staggerFromTo(
      ".image",
      0.5,
      { y: "0", opacity: 0 },
      { y: "0", opacity: 1 }
    );
    new ScrollMagic.Scene({
      triggerElement: ".side",
      triggerHook: 0.9,
      duration: 0,
    })
      .setTween(timelineOne)
      .addTo(controller);
    // eslint-disable-next-line
  }, []);
  
  return (
    <div className="side" style={style} id={id}>
        {((side === "left" && (window.innerWidth > 1023 || !swapOnMobile)) || (side ==="right" && window.innerWidth < 1024 && swapOnMobile)) && (<div className="sideImgContainer"><img src={imgSrc} id="image" className="sideImg"/></div>)}
        <div className="sideContent">
            <div className="sideContentContainer">
                <div className="sideCaption">{caption}</div>
                <div className="sideText">{text}</div>
            </div>
            
        </div>
        {((side === "right" && (window.innerWidth > 1023 || !swapOnMobile)) || (side ==="left" && window.innerWidth < 1024 && swapOnMobile)) && (<div className="sideImgContainer"><img src={imgSrc} id="image" className="sideImg"/></div>)}
    </div>
  );
};

export default SideImg;
