import React, { useState, useEffect } from "react";
import "./LearnMoreNav.scss";
import { useHistory } from "react-router-dom";
import useWindowSize from "../../../../Hooks/UseWindowSize";

const navigationPositions = [
  { name: "Ciasto", id: 1 },
  { name: "Mąka", id: 2 },
  { name: "Wędliny", id: 3 },
  { name: "Kurczka", id: 4 },
  { name: "Warzywa", id: 5 },
  { name: "Ser", id: 6 },
  { name: "Sosy", id: 7 },
];

const LearnMoreNav = ({ isMobileProductListOpen, onClose }) => {
  const [open, setOpen] = useState(false);
  const [popoverOpen, setPopoverOpen] = useState(false);
  const [showMobileProductsList, setShowMobileProductsList] = useState(false);
  const size = useWindowSize();
  const { width } = size;
  const history = useHistory();
  var navbar = null;
  var navYOffset = 0;
  var chosenElem = null;
  var chosenElemBtn = null;

  function stickNav() {
    if (navbar == null) return;
    if (window.pageYOffset >= navbar.offsetTop) {
      navbar.classList.add("learnMoreNavSticky")
    }
    if (window.pageYOffset < navYOffset) {
      navbar.classList.remove("learnMoreNavSticky");
    }

  }

  function updateSize() {
    if (navbar != null) navYOffset = navbar.offsetTop;
  }

  useEffect(() => {
    navbar = document.getElementById("learnMoreNavbar");
    navYOffset = navbar.offsetTop;

    window.addEventListener('resize', updateSize);
    if (isMobileProductListOpen) {
      setPopoverOpen(true);
      setShowMobileProductsList(true);
    }

    window.onscroll = function () { stickNav() };
  }, [isMobileProductListOpen, onClose]);

  const navigationHandler = (id, device) => {
    if (chosenElemBtn != null) chosenElemBtn.classList.remove("activeLearnMoreNavElem");

    if (id == 1) {
      chosenElem = document.getElementById("ciasto");
    } else if (id == 2) {
      chosenElem = document.getElementById("maka");
    } else if (id == 3) {
      chosenElem = document.getElementById("wedliny");
    } else if (id == 4) {
      chosenElem = document.getElementById("kurczak");
    } else if (id == 5) {
      chosenElem = document.getElementById("warzywa");
    } else if (id == 6) {
      chosenElem = document.getElementById("ser");
    } else if (id == 7) {
      chosenElem = document.getElementById("sosy");
    }
    if (chosenElem != null) {
      if (chosenElemBtn != null) chosenElemBtn.classList.remove("activeLearnMoreNavElem");
      for(var i = 1; i <= navigationPositions.length; i++) {
        document.getElementById("learnMoreNavBtn" + i).classList.remove("activeLearnMoreNavElem");
      }

      chosenElemBtn = document.getElementById("learnMoreNavBtn" + id);
      chosenElemBtn.classList.add("activeLearnMoreNavElem");
      chosenElem.scrollIntoView({ behavior: "smooth", block: "end" });
    }
  };

  const openProductsHandler = (id) => {
    if (id === 1) {
      setOpen(true);
    } else {
      setOpen(false);
    }
  };

  const generateHref = (id) => {
    if (id === 1) {
      return "/dowiedz-sie-wiecej/#ciasto";
    }
    if (id === 2) {
      return "/dowiedz-sie-wiecej/#maka";
    }
    if (id === 3) {
      return "/dowiedz-sie-wiecej/#wedliny";
    }
    if (id === 4) {
      return "/dowiedz-sie-wiecej/#kurczak";
    }
    if (id === 5) {
      return "/dowiedz-sie-wiecej/#warzywa";
    }
    if (id === 6) {
      return "/dowiedz-sie-wiecej/#ser";
    }
    if (id === 7) {
      return "/dowiedz-sie-wiecej/#sosy";
    }

    return "/";
  };

  return (
    <div className="learnMoreNav" id="learnMoreNavbar">
      <ul className="learnMoreNavList">
        {navigationPositions.map((position) => {
          return (
            <a
              key={position.id}
              className="learnMoreNavListElem"
              id={"learnMoreNavBtn" + position.id}
              onClick={() => navigationHandler(position.id, "desktop")}
              onMouseEnter={() => openProductsHandler(position.id)}
            >
              <li>{position.name.toUpperCase()}</li>
            </a>
          );

        })}
      </ul>
    </div>
  );
};

export default LearnMoreNav;
